.header {
    background-color: #322c5a;
    padding-bottom: 1rem;
    position: sticky;
    top: 0;
}

.buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.buttons > * {
    margin: 0 0.5rem;
}

.overview {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0 1rem;
}

.overview > * {
    --element-margin: 1rem;
    margin: var(--element-margin);
    flex: 0 0 calc((100% / 3) - (var(--element-margin) * 2));
    width: calc((100vw / 3) - (var(--element-margin) * 2));
}

@media (max-width: 750px) {
    .overview > * {
        flex: 0 0 calc((100% / 2) - (var(--element-margin) * 2));
        width: calc((100vw / 2) - (var(--element-margin) * 2));
    }
}

@media (max-width: 500px) {
    .overview > * {
        flex: 0 0 calc(100% - (var(--element-margin) * 2));
        width: calc(100vw - (var(--element-margin) * 2));
    }
}

.flexColumn {
    display: flex;
    flex-direction: column;
}
