.header {
    background-color: #322c5a;
    padding-bottom: 1rem;
    position: sticky;
    top: 0;
}

.buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.buttons > * {
    margin: 0 0.5rem;
}

.overview {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0 1rem;
}

.overview > * {
    --element-margin: 1rem;
    --elements-per-row: 2;

    margin: var(--element-margin);
    flex: 0 0 calc((100% / var(--elements-per-row)) - (var(--element-margin) * 2));
    width: calc((100vw / var(--elements-per-row)) - ((var(--element-margin) * 2) * var(--elements-per-row)));
}

@media (max-width: 500px) {
    .overview > * {
        --elements-per-row: 1;
    }
}
