.card > * {
    border-left: 1px solid #fff;
    border-right: 1px solid #fff;
}

.card > :first-child {
    border-top: 1px solid #fff;
    border-radius: 5px 5px 0 0;
}

.card > :last-child {
    border-bottom: 1px solid #fff;
    border-radius: 0 0 5px 5px;
}


.card > :first-child:last-child {
    border-bottom: 1px solid #fff;
    border-top: 1px solid #fff;
    border-radius: 5px;
}

.cardClickable {
    composes: card;
    cursor: pointer;
}
