.cardTitle {
    background-color: #fff;
    color: #322c5a;
    font-size: 4rem;
    margin: 0;
    padding: 0 1rem;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    text-align: center;
}

.cardTitleClickable {
    composes: cardTitle;
    cursor: pointer;
}
