.button {
    background-color: transparent;
    border: 1px solid white;
    border-radius: 50%;
    color: white;
    cursor: pointer;
    height: 50px;
    width: 50px;
}


.accept {
    composes: button;
    border: 1px solid #00ff00;
    color: #00ff00;
}

.accept::before {
    content: "ðŸ—¸";
}

.accept:not(:disabled):hover {
    background-color: rgba(0, 255, 0, 0.1);
}

.accept:disabled {
    border: 1px solid #00ff0033;
    color: #00ff0033;
    cursor: not-allowed;
}


.add {
    composes: button;
    border: 1px solid #fff;
    color: #fff;
}

.add::before {
    content: "+";
}

.add:not(:disabled):hover {
    background-color: rgba(255, 255, 255, 0.1);
}

:disabled {
    border: 1px solid #ffffff33;
    color: #ffffff33;
    cursor: not-allowed;
}


.deny {
    composes: button;
    border: 1px solid red;
    color: red;
}

.deny::before {
    content: "âœ•";
}

.deny:not(:disabled):hover {
    background-color: rgba(255, 0, 0, 0.1);
}

.deny:disabled {
    border: 1px solid #ff000033;
    color: #ff000033;
    cursor: not-allowed;
}


.trash {
    composes: button;
    border: 1px solid #aaaaaa;
    color: #aaaaaa;
    margin: 1rem;
}

.trash::before {
    content: "Delete";
}

.trash:not(:disabled):hover {
    background-color: rgba(127, 127, 127, 0.1);
}

.trash:disabled {
    border: 1px solid #aaaaaa33;
    color: #aaaaaa33;
    cursor: not-allowed;
}
